import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';

@Component({
  selector: 'app-wallet',
  templateUrl: './wallet.component.html',
  styleUrls: ['./wallet.component.scss']
})
export class WalletComponent implements OnInit {
  keySearch: any;
  sortby: any = 'AZ';
  list_wallet: any = [];
  private observableWallets: any;
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef
  ) {
    if (this.router.getCurrentNavigation().extras.state) {
      this.keySearch = this.router.getCurrentNavigation().extras.state.keySearch;
      this.sortby = this.router.getCurrentNavigation().extras.state.sortby;
    }
  }

  ngOnInit(): void {
    this.getDataWallets();
  }
  ngOnDestroy() {
    this.observableWallets.unsubscribe();
  }
  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector("#product-list") && document.querySelector(".ant-table-thead") && document.querySelector(".product-list-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#product-list").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".product-list-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 100 + "px";
    }
    this.cdRef.detectChanges();
  }
  /**Lấy danh sách tài khoản thanh toán */
  getDataWallets() {
    this.observableWallets = this.vhQueryCafe.getObservableLocalWallets().subscribe((localwallets: any) => {
      let data = localwallets.filter(item => item._id != 'id_cash' && item.id_branch == this.vhQueryCafe.getDefaultBranch()._id);
      if (this.keySearch) {
        this.list_wallet = this.vhAlgorithm.searchList(
          this.keySearch,
          data,
          ["name", "bank_name", "number"]
        );
      } else {
        this.list_wallet = data;
      }
      this.sortData(this.sortby);
    })
  }

  goBack() {
    this.router.navigate(['/cafe/dashboard/management/cashflow']);
  }

  /**Search danh sách tài khoản thanh toán */
  searchWallet(e) {
    this.keySearch = e;
    let value = this.vhAlgorithm.changeAlias(e.toLowerCase());
    let data = this.vhQueryCafe.getlocalWallets().filter(item => item._id != 'id_cash' && item.id_branch == this.vhQueryCafe.getDefaultBranch()._id);
    this.list_wallet = this.vhAlgorithm.searchList(value, data, ["name", "bank_name", "number"]);
    this.sortData(this.sortby);
  }

  /**
   * sắp xếp tài khoản thanh toán bằng tên theo bảng chữ cái anphabet
   * @param event giá trị sắp xếp
   */
  changeSort(event) {
    this.sortby = event;
    this.sortData(this.sortby);
  }

  sortData(sortby: any) {
    if (sortby === "AZ") {
      //sắp xếp từ A -> Z
      this.list_wallet = this.vhAlgorithm.sortStringbyASC([...this.list_wallet], 'name');
    } else {
      //sắp xếp từ Z -> A
      this.list_wallet = this.vhAlgorithm.sortStringbyDESC([...this.list_wallet], 'name');
    }
  }

  gotoAddWallet() {
    this.router.navigate(['/cafe/dashboard/management/cashflow/wallet/add'], {
      state: {
        keySearch: this.keySearch,
        sortby: this.sortby
      }
    });
  }

  /* -----Pagination----- */
  onCurrentPageDataChange(value) {
  }

  /**--------------------- */

  gotoWalletDetail(data) {
    this.router.navigate(['/cafe/dashboard/management/cashflow/wallet/detail'], {
      state: {
        data: data,
        keySearch: this.keySearch,
        sortby: this.sortby
      }
    })
  }
}
